import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Vant from 'vant'
import axios from "axios"
// 引入第三方的日期格式处理的工具库
import moment from "moment";
import {
  Toast
} from 'vant';

let app;

// 绑定到原型
Vue.prototype.$axios = axios;
// 设置默认基地址
// axios.defaults.baseURL = "http://192.168.1.102:3000";

// 线上地址
axios.defaults.baseURL = "http://hmtoutiao-api.atlansic.com"

// 全局引用 vant ui插件
Vue.use(Vant);

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  return response;
}, function (error) {
  // 对响应错误做点什么
  // error 是 js原生错误对象，通过error.response开获取到详细信息
  const {
    statusCode,
    message
  } = error.response.data;
  //判断用户密码错误
  if (statusCode == 400) {
    Toast.fail(message);
  }
  if (statusCode == 403) {
    // 跳转的时候吧上一页的路径获取

    // 提示
    Toast.fail(message);
    app.$router.push({
      path: "/login",
      query: {
        return_url: app.$route.path
      }
    });
    console.log(app.$route.path);


  }
  return Promise.reject(error);
});

// 添加全局的路由守卫
router.beforeEach((to, from, next) => {

  // 添加网页头部标题
  document.title = to.meta.title
  // to 指即将要去的页面
  if (to.meta.authorization) {
    const tokenStr = JSON.parse(localStorage.getItem('user_info')) || {};
    if (tokenStr.token) {
      next();
    } else {
      // 获取上一页的地址
      app.$router.push({
        path: "/login",
        query: {
          return_url: app.$route.path
        }
      });

      // next('/login');
    }
  } else {
    next();
  }
})
import {
  Form
} from 'vant';
Vue.config.productionTip = false

app = new Vue({
  router,
  mode: 'history', // 加上这个配置项，url默认的 # 就不会出现了
  render: h => h(App)
}).$mount('#app')