import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [{
    path: "/",
    redirect: "/index"
  },
  {
    path: "/index",
    meta: {
      title: "首页"
    },
    component: () => import("../views/index.vue")
  }, {
    // 登录页面
    path: "/login",
    meta: {
      title: "登录"
    },
    // 使用懒加载
    component: () => import("../views/login.vue")
  },
  {
    // 注册页面
    path: "/regesiter",
    meta: {
      title: "注册"
    },
    // 使用懒加载
    component: () => import("../views/regesiter.vue")
  },
  {
    // 个人中心
    path: "/myuser",
    // meta 用于添加头部信息
    meta: {
      title: "个人中心",
      authorization: true
    },
    // 使用懒加载
    component: () => import("../views/myuser.vue")
  },
  {
    path: "/edit-profile",
    // 使用懒加载
    component: () => import("../views/editprofile.vue")
  },
  {
    // 我的关注
    path: "/attention",
    component: () => import("@/views/attention.vue"),
    meta: {
      title: "我的关注",
      authorization: true
    },


  },
  { // 我的跟帖
    path: "/myfollow",
    component: () => import("@/views/myfollow.vue"),
    meta: {
      title: "我的跟帖",
      authorization: true
    }
  }, {
    path: "/mycollect",
    component: () => import("@/views/mycollect.vue"),
    meta: {
      title: "我的收藏",
      authorization: true
    }
  },
  {
    path: "/cloumn",
    component: () => import("@/views/cloumn.vue"),
    meta: {
      title: "栏目管理",
      authorization: true
    }
  }, {
    path: "/search",
    component: () => import("@/views/search"),
    meta: {
      title: "搜索",
      authorization: true
    }
  }, {
    path: "/detail/:id",
    component: () => import("@/views/detail"),
    meta: {
      title: "新闻详情",
    }
  },
  {
    path: "/comment/:id",
    component: () => import("@/views/comment"),
    meta: {
      title: "评论列表",
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router